body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen 
  and (max-device-width: 667px) {
    div#scrollingBackground {
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        z-index: -1000;
        background: url(/illustratio/static/media/blurredArt.3c356756.jpg) repeat-y;
        -webkit-animation: 50s scroll infinite linear;
                animation: 50s scroll infinite linear;
    }

    p, label {
        text-shadow: 0 0 2px white, 0 0 10px white;
    }

    @-webkit-keyframes scroll{
      100%{
        background-position: 0px -1000px;
      }
    }

    @keyframes scroll{
      100%{
        background-position: 0px -1000px;
      }
    }
}

.fake-input-group input[disabled] {
    background-color: transparent;
    background-color: initial;
}
button[data-target="#visited"][aria-expanded="true"]:before {
    content: "\2713   ";
}
/*div.btn-group-toggle[data-toggle="buttons"] label.active:before {
    content: "\2665 ";
}*/

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

